function useTodoLists(currentUserId, setLoaded) {
  const [todoLists, setTodoLists] = React.useState([]);

  // useEffect(() => {
  //   firebase
  //     .firestore()
  //     .collection(currentUserId)
  //     .onSnapshot((snapShot) => {
  //       const newTodoLists = [];
  //       snapShot.docs.map((doc) => {
  //         let tempData = {};
  //         tempData = doc.data();
  //         tempData.id = doc.id;
  //         newTodoLists.push(tempData);
  //       })
  //       setTodoLists(newTodoLists);
  //     })
  // }, []);
  useEffect(() => {
    firebase
      .firestore()
      .collection("boards")
      .where("assignments", "array-contains", currentUserId)
      .get()
      .then((snapShot) => {
        const newTodoLists = [];
        snapShot.docs.map((doc) => {
          let tempData = {};
          tempData = doc.data();
          tempData.id = doc.id;
          newTodoLists.push(tempData);
        });
        setTodoLists(newTodoLists);
        setLoaded(true);
      })
      .catch((error) => {
        alert("error getting documents");
        console.log("Error getting documents: ", error);
      });
  }, []);
  return todoLists;
}

const Profile = (props) => {
  const currentUserId = props.currentUserId; //firebase.auth().currentUser.email;
  const userUid = firebase.auth().currentUser.uid;
  // const username = props.currentUsername;

  const currentUserEmail =
    firebase.auth().currentUser && firebase.auth().currentUser.email;
  const username =
    firebase.auth().currentUser && firebase.auth().currentUser.displayName;
  if (!userUid) {
    // not logged in
    alert("Please login first");
    props.history.replace("/");

    return null;
  }

  // eslint-disable-next-line react-hooks/rules-of-hooks
  const [loaded, setLoaded] = useState(false);
  // eslint-disable-next-line react-hooks/rules-of-hooks
  const [position, setPosition] = useState("");
  // eslint-disable-next-line react-hooks/rules-of-hooks
  const [name, setName] = useState("");
  // eslint-disable-next-line react-hooks/rules-of-hooks
  const [imgUrl, setImgUrl] = useState("");

  // eslint-disable-next-line react-hooks/rules-of-hooks
  const tasksData = useTodoLists(currentUserId, setLoaded);
  // eslint-disable-next-line react-hooks/rules-of-hooks
  const [numOfAssignedToOthers, setNumOfAssignedToOthers] = useState(0);
  // eslint-disable-next-line react-hooks/rules-of-hooks
  const [numOfAssignedToOthersCompleted, setNumOfAssignedToOthersCompleted] = useState(0);
  // eslint-disable-next-line react-hooks/rules-of-hooks
  const [numOfAssignedToOthersRemaining, setNumOfAssignedToOthersRemaining] = useState(0);
  // eslint-disable-next-line react-hooks/rules-of-hooks
  const [numOfAssignedToMyself, setNumOfAssignedToMyself] = useState(0);
  // eslint-disable-next-line react-hooks/rules-of-hooks
  const [numOfAssignedToMyselfCompleted, setNumOfAssignedToMyselfCompleted] = useState(0);
  // eslint-disable-next-line react-hooks/rules-of-hooks
  const [numOfAssignedToMyselfRemaining, setNumOfAssignedToMyselfRemaining] = useState(0); 
  // eslint-disable-next-line react-hooks/rules-of-hooks
  const [numOfAssignee, setNumOfAssignee] = useState(0);
  // eslint-disable-next-line react-hooks/rules-of-hooks
  const [totalTasks, setTotalTasks] = useState(0);
  // eslint-disable-next-line react-hooks/rules-of-hooks
  const [remainingAssigneeTasks, setRemainingAssigneeTasks] = useState(0);
  // eslint-disable-next-line react-hooks/rules-of-hooks
  const [assigneeCompletedTasks, setAssigneeCompletedTasks] = useState(0);
  // eslint-disable-next-line react-hooks/rules-of-hooks
  const [numOfBoardsCompleted, setNumOfBoardsCompleted] = useState(0);
  // eslint-disable-next-line react-hooks/rules-of-hooks
  // const [assigneeCompletedTasks, setAssigneeCompletedTasks] = useState(0);

  let completionRate = Math.round(( (assigneeCompletedTasks+numOfAssignedToMyselfCompleted) * 100) / (numOfAssignedToMyself+numOfAssignee));

  // console.log('dataaa', tasksData);

  // eslint-disable-next-line react-hooks/rules-of-hooks
  useEffect(() => {
    firebase
      .firestore()
      .collection("users")
      .doc(userUid)
      .get()
      .then(async (doc) => {
        await setPosition(doc.data().position);
        await setName(doc.data().name);
        await setImgUrl(doc.data().imgUrl);
      })
      .catch((err) => console.log(err));
  }, []);

  //   // eslint-disable-next-line react-hooks/rules-of-hooks
  //   useEffect(()=>{
  //     setImgUrl(imgUrl);
  //   },[imgUrl])

  // eslint-disable-next-line react-hooks/rules-of-hooks
  useEffect(() => {
    if (tasksData.length > 0) {
      console.log("dataaa", tasksData);
      tasksData.map(async (boardObj) => {
        let tasksCompletedInBoard = 0;
        // let toto = totalTasks+boardObj.todoList.length;
        await setTotalTasks(
          (prevState) => prevState + boardObj.todoList.length
        );
        // console.log(await totalTasks);
        if(boardObj.todoList.length>0){
          boardObj.todoList.map(async (task) => {
            if(task.isComplete) {
              tasksCompletedInBoard += 1;
            }
            if (
              task.assignedBy === currentUserId &&
              task.assignedTo !== "" &&
              task.assignedTo !== currentUserId
            ) {
                await setNumOfAssignedToOthers((prevState) => prevState + 1);
                if(task.isComplete) {
                  setNumOfAssignedToOthersCompleted(prev=>prev+1);
                }
                else setNumOfAssignedToOthersRemaining(prev=>prev+1);
              }
            if (
              task.assignedBy === currentUserId &&
              task.assignedTo !== "" &&
              task.assignedTo === currentUserId
            ) {
                await setNumOfAssignedToMyself((prevState) => prevState + 1);
                if(task.isComplete) {
                  setNumOfAssignedToMyselfCompleted(prev => prev + 1 );
                }
                else setNumOfAssignedToMyselfRemaining(prev => prev + 1 );
              }
            if (
              task.assignedTo === currentUserId 
              // task.assignedBy !== currentUserId && // CHANGE THIS SO BOTH ASSIGNEE SELF AND ASSIGNEE OF OTHERS ARE DIFFERENT METRICS
              && task.assignedBy !== currentUserId
            ) {
              await setNumOfAssignee((prevState) => prevState + 1);
              if (task.isComplete) {
                await setAssigneeCompletedTasks((prevState) => prevState + 1);
              } else {
                await setRemainingAssigneeTasks((prevState) => prevState + 1);
              }
            }
          });
        }
        if(tasksCompletedInBoard===boardObj.todoList.length && tasksCompletedInBoard!==0) setNumOfBoardsCompleted(prev=>prev+1);
      });
      // console.log('assigned:', numOfAssignedToOthers, 'assignee:', numOfAssignee, 'total:',
      // totalTasks, 'remaining:', remainingAssigneeTasks, 'completedTasks', completedTasks);
    }
  }, [tasksData.length]);

  return loaded ? (
    <div className="w-full mt-5">
      <span
        className="w-full"
      >
        <div>
          <div>
            <Paper className="p-5 mx-5">
              <span className="flex items-start justify-start">
                <span
                  style={{height:'450px'}}
                  className="w-1/3 space-x-3 space-y-4 col-start"
                >
                  <span className="mx-auto space-y-4 col-center">
                    <Avatar
                        // variant="square"
                        style={{ width: "180px", height: "180px" }}
                        alt={name}
                        src={imgUrl}
                      />
                      <span className="space-y-1 text-base font-medium text-gray-400 col-center">
                        <span>{username}</span>
                        <span>{currentUserId}</span>
                      </span>
                  </span>
                  {console.log("URLLLLLLL", imgUrl)}
                  <span className="w-full space-x-3 row-center">
                    <BootstrapTooltip placement="top" title="coming soon">
                      <span><GithubIcon className="w-10 h-10 text-green" /></span>
                    </BootstrapTooltip>
                    <BootstrapTooltip placement="top" title="coming soon">
                      <span><LinkedInIcon className="w-10 h-10 text-green" /></span>
                    </BootstrapTooltip>
                    <BootstrapTooltip placement="top" title="coming soon">
                      <span><LinkRounded className="w-10 h-10 text-green" /></span>
                    </BootstrapTooltip>
                  </span>
                </span>
                <span className="w-2/3 h-full row-start">
                  <span className="w-2/5 h-full space-y-4 col-start"> 
                      {/* USERNAME + POSITION */}
                    <span className="flex flex-col w-full space-y-4">
                      <span className="text-lg text-gray-800 list-none col-start h-18">
                        <span className="text-2xl font-semibold text-gray-600">
                          <span>{name}</span>
                        </span>
                        {
                          position ? 
                          <span className="text-sm font-medium text-green-light">
                            <span>{position}</span>
                          </span>
                          :
                          <span className="text-sm font-medium text-green-light">
                            <span>Position: N/A</span>
                          </span>
                        }
                      </span>
                      {/* RATING */}
                      <span className="flex flex-col justify-center space-y-1 h-28">
                        <span className="text-xs font-medium text-gray-400 ">TODUE SCORE</span>
                        <span className="flex items-center justify-start space-x-2">
                          <BootstrapTooltip placement="top" title="This is your completion rate">  
                            <span className="text-4xl font-bold text-green">{completionRate ? completionRate : 0}%</span>
                          </BootstrapTooltip>
                          <Rating precision={0.5} style={{color:'#3dd19d'}} name="read-only" value={completionRate ? (completionRate*5)/100 : 0} readOnly />
                        </span>
                      </span>
                    </span>
                    {/* STATS */}
                    <span className="space-y-8 col-start">
                      <span className="space-y-2 col-start">
                        <span className="text-xs font-medium text-gray-400">COMPLETED TASKS</span>
                        <span className="h-8 text-2xl font-bold text-center rounded-full w-28 bg-opacity-80 bg-green-lightest text-green">
                          {numOfAssignedToMyselfCompleted+assigneeCompletedTasks}
                        </span>
                      </span>
                      <span className="space-y-2 col-start">
                        <span className="text-xs font-medium text-gray-400">INCOMPLETE TASKS ACROSS ALL BOARDS</span>
                        <span className={`h-8 text-2xl font-bold text-center ${remainingAssigneeTasks+numOfAssignedToMyselfRemaining===0 ? "text-green bg-green-lightest" : "text-red-400 bg-red-100"} rounded-full w-28 bg-opacity-80`}>
                          {remainingAssigneeTasks + numOfAssignedToMyselfRemaining}
                        </span>
                      </span>
                      <span className="space-y-2 col-start">
                        <span className="text-xs font-medium text-gray-400">BOARDS COMPLETED (ALL TIME)</span>
                        <span className="h-8 text-2xl font-bold text-center rounded-full w-28 bg-opacity-80 bg-green-lightest text-green">
                          {numOfBoardsCompleted}
                        </span>
                      </span>
                    </span>
                  </span>
                  <span className="h-full">
                    <Charts
                      // numOfAssignedToMyselfRemaining={numOfAssignedToMyselfRemaining}
                      // remainingAssigneeTasks={remainingAssigneeTasks} 
                    />
                  </span>
                </span>
              </span>
              {/* <Box marginLeft={10}>
                <ul>
                  <li>
                    <Typography>My Total tasks: 
                      <Typography component="span" color="primary"> {totalTasks}</Typography>
                    </Typography>
                  </li>
                  <li>
                    <Typography>Assigned by ME to OTHERS: 
                      <Typography component="span" color="primary">{numOfAssignedToOthers}</Typography>
                    </Typography>
                  </li>
                  <li>
                    <Typography>Assigned by ME to OTHERS completed: 
                      <Typography component="span" color="primary">{numOfAssignedToOthersCompleted}</Typography></Typography>
                  </li>
                  <li>
                    <Typography>Assigned by ME to OTHERS remaining: 
                      <Typography component="span" color="primary">{numOfAssignedToOthersRemaining}</Typography></Typography>
                  </li>
                  <li>
                    <Typography>Assigned to Self Tasks: 
                      <Typography component="span" color="primary">{numOfAssignedToMyself}</Typography></Typography>
                  </li>
                  <li>
                    <Typography>Assigned to Self Tasks completed: 
                      <Typography component="span" color="primary">{numOfAssignedToMyselfCompleted}</Typography></Typography>
                  </li>
                  <li>
                    <Typography>Assigned to Self Tasks remaining: 
                      <Typography component="span" color="primary">{numOfAssignedToMyselfRemaining}</Typography></Typography>
                  </li>
                  <li>
                    <Typography>
                      Tasks assigned to ME by OTHERS: 
                      <Typography component="span" color="primary">{numOfAssignee}</Typography>
                    </Typography>
                  </li>
                  <li>
                    <Typography>
                      Tasks assigned to ME by OTHERS completed: 
                      <Typography component="span" color="primary">{assigneeCompletedTasks}</Typography>
                    </Typography>
                  </li>
                  <li>
                    <Typography>
                      Tasks assigned to ME by OTHERS remaining: 
                      <Typography component="span" color="primary">{remainingAssigneeTasks}</Typography>
                    </Typography>
                  </li>
                  <li>
                    <Typography>
                      Completion Score for tasks assigned to ME by OTHERS: 
                      <Typography component="span" color="primary">{`${Math.round((assigneeCompletedTasks * 100) / numOfAssignee)}%`}</Typography>
                    </Typography>
                  </li>
                  <li>
                    <Typography>
                      Completion Score for tasks assigned by ME to OTHERS: 
                      <Typography component="span" color="primary">{`${Math.round((numOfAssignedToOthersCompleted * 100) / numOfAssignedToOthers)}%`}</Typography>
                    </Typography>
                  </li>
                  <li>
                    <Typography>
                      Completion Score for assigned to Self Tasks:
                      <Typography component="span" color="primary">{`${Math.round((numOfAssignedToMyselfCompleted * 100) / numOfAssignedToMyself)}%`}</Typography>
                    </Typography>
                  </li>
                </ul>
              </Box> */}
            </Paper>
          </div>
        </div>
      </span>
    </div>
  ) : (
    <div id="loader">
      <CircularProgress style={{ color: "#3dd19d" }} />
    </div>
  );
};

export default Profile;
